



@media screen and (max-width:768px){

  #root     {

    font-size: 0.9rem;
}
}

@media screen and (max-width:600px){

   #root{

    font-size: 0.85rem;
}

.posts .buttons button:nth-child(3){
    margin-left: 10px;
    
}
}


@media screen and (max-width:400px){

 #root{

    font-size: 0.80rem;
}

td, th,button {

   padding: 5px 10px;
}

}

@media screen and (max-height:400px){

    .calendarWrapper{
        min-height: calc(100vh - 20vh);
    }

}